import React, { Component } from 'react';
import '../styles/feedback.css';
import fbPic from '../images/sally.png';
import sound from '../media/nyeselkanloe.wav';

class Feedback extends Component {
    validate() {
        var fName = document.getElementById("fName").value;
        var fEmail = document.getElementById("fEmail").value;
        var fFeedback = document.getElementById("fFeedback").value;

        if(fName === "" || fEmail === "" || fFeedback === ""){
            document.getElementById("errMsg").innerHTML = "Please FILL your data information!";
            document.getElementById("errMsg").style.visibility = "visible";
        }
        else if(fName.length < 5 || fName.length > 25){
            document.getElementById("errMsg").innerHTML = "Name must be filled with 5 to 25 characters";
            document.getElementById("errMsg").style.visibility = "visible";
        }
        else if(fEmail.indexOf("@") == -1) {
            document.getElementById("errMsg").innerHTML = "Email should consist ONE '@' character";
            document.getElementById("errMsg").style.visibility = "visible";
        }
        else if(fEmail.indexOf(".") == -1) {
            document.getElementById("errMsg").innerHTML = "Email should consist '.' character";
            document.getElementById("errMsg").style.visibility = "visible";
        }
        else{
            document.getElementById("errMsg").innerHTML = "Thank you for the feedback 😊";
            document.getElementById("errMsg").style.color = "green";
            document.getElementById("errMsg").style.visibility = "visible";
            document.getElementById("fName").value = "";
            document.getElementById("fEmail").value = "";
            document.getElementById("fFeedback").value = "";
        }

    }
    reset(){
        document.getElementById("fName").value = "";
        document.getElementById("fEmail").value = "";
        document.getElementById("fFeedback").value = "";
        document.getElementById("errMsg").style.visibility = "hidden";
    }

    render(){
        return(
            <div>
                <div className="feedbackHome">

                    <div className="feedbackContainer">
                        <div className="headerInfo">
                            <p>Feedback</p>
                        </div>
                        <div className="feedbackContent">
                            <form class="feedbackForm">
                                <div class="feedbackItem">
                                    <input type="text" placeholder="name" id="fName" />
                                </div>
                                <div class="feedbackItem">
                                    <input placeholder="email" id="fEmail"/>
                                </div>
                                <textarea class="feedbackInput" cols="45" rows="5" placeholder="feedback" id="fFeedback"></textarea>
                                <p id="errMsg">Error Msg</p>
                                
                                <div className="btnContainer">
                                    <div class="btnReset"  onClick={this.reset}>Reset</div>
                                    <div class="btnSubmit" onClick={this.validate}>Submit</div>
                                </div>
                            </form>
                        </div>
                    </div>
                    <div className="feedbackPic">
                        <div className="picWrap">
                            <img src={fbPic} className="fbPic"></img>
                        </div>
                    </div>
                </div>
                <audio controls loop autoPlay hidden >
                    <source src={sound} />
                </audio>
            </div>
        );
    }
}

export default Feedback;