import React, { Component } from 'react';
import '../styles/home.css';
import bg from '../images/profilebg.jpg';
import dp from '../images/dp.jpg';

class Home extends Component {
    setBounceBall() {
        const canvas = document.getElementById('bolaPantul');
        let context = canvas.getContext('2d');

        canvas.width=window.innerWidth;
        canvas.height=window.innerHeight-80;

        let posisi = { x: 25, y: 25 };
        let velo = 3, corner = 50, rad = 20;
        let ball = { x: posisi.x, y: posisi.y };
        let moveX = Math.cos(Math.PI / 180 * corner) * velo;
        let moveY = Math.sin(Math.PI / 180 * corner) * velo;

        const draw = () => {
            context.clearRect(0, 0, window.innerWidth, window.innerHeight);

            if (ball.x > canvas.width - rad || ball.x < rad) {
                moveX = -moveX;
            }
            if (ball.y > canvas.height - rad || ball.y < rad){
                moveY = -moveY;
            }

            ball.x += moveX;
            ball.y += moveY;

            context.beginPath();
            context.fillStyle = 'red';
            context.arc(ball.x, ball.y, rad, 0, Math.PI * 2, false);
            context.fill();
            context.closePath();
        }
        setInterval(draw, 5);
    }

    drawBall(contex, ball, canvas, moveX, moveY) {
        let context = contex;

    }

    load() {
        var temp = localStorage.getItem('num');
        var bal = localStorage.getItem('bal');
        if (temp) {
            console.log(temp);
            document.getElementById("num").value = temp;
        }
    }

    componentDidMount() {
        this.load();
        this.setBounceBall();
    }

    render() {
        return (
            <div>
                <div className="home">
                    <div className="profile">
                        <img src={bg} className="probg"></img>
                        <img src={dp} className="dp"></img>
                        <p>Welcome,</p>
                        <input className="dispNum" id="num" readOnly></input>
                        <button className="proEdit">
                            <p>Edit Profile</p>
                        </button>
                    </div>

                    <div className="info">
                        <div className="headerInfo">
                            <p>Home</p>
                        </div>
                        <div className="infocontent">
                            <div>
                                <p className="infoTxt">SL Balance</p>
                                <p className="balTxt">Rp. 125000</p>
                            </div>
                            <p className="infoTxt">Data Package</p>
                            <div className="quotaContainer">
                                <div class="dataContainer">
                                    <div class="dataTxtContainerHome">
                                        <p>Internet 3G/4G</p>
                                        <p>6/10GB</p>
                                    </div>
                                    <input class="range" type="range" name="" disabled="" value="60" />
                                </div>
                                <div class="dataContainer">
                                    <div class="dataTxtContainerHome">
                                        <p>YouTube</p>
                                        <p>3/6GB</p>
                                    </div>
                                    <input class="range" type="range" name="" max="60" disabled="" value="30" />
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <canvas id="bolaPantul"></canvas>
            </div>
        );
    }
}

export default Home;