import React, {Component} from 'react';
import { NavLink } from "react-router-dom";
import logo from '../images/logo.png';
import '../styles/navigation.css';

class Navigation extends Component {
    logout(){
        localStorage.removeItem('num');
        localStorage.removeItem('bal');
    }
    menuclicked(){
        var temp = localStorage.getItem('nav');

        if(temp == 1){
            document.getElementById("navid").style.marginBottom ="350px";
            localStorage.setItem('nav', 2);
        }
        else{
            document.getElementById("navid").style.marginBottom ="0px";
            localStorage.setItem('nav', 1);
        }
    }
    navupdate(){
        localStorage.setItem('nav', 1);
        document.getElementById("navid").style.marginBottom ="0px";
        document.getElementById("toggle").checked = false;
    }

    render(){
        return(
            <div className="nav" id="navid">
               <img src={logo} className="imgLogo"></img>
                <label onClick="" for="toggle">&#9776;</label>
                <input type="checkbox" id="toggle" onClick={this.menuclicked}/>

                <div className="menu">
                    <NavLink className="navText" to='/home' onClick={this.navupdate}>Home</NavLink>
                    <NavLink className="navText" to='/mypackage' onClick={this.navupdate}>MyPackage</NavLink>
                    <NavLink className="navText" to='/promo' onClick={this.navupdate}>Promo</NavLink>
                    <NavLink className="navText" to='/feedback' onClick={this.navupdate}>Feedback</NavLink>
                    <NavLink className="navText" to='/' onClick={this.logout}>Logout</NavLink>
                </div>
            </div>
        );
    }
}

export default Navigation;