import React, {Component} from 'react';
import {Redirect} from "react-router-dom";
import logo from '../images/logo.png';
import bg from '../images/bg1.png';
import '../styles/login.css';

class Login extends Component {
  constructor(props){
    super(props);

    this.state = {
      toHome: false
    };
    this.validate = this.validate.bind(this);
  }

  validate() {
      var number = document.getElementById("numfield").value;
      var pass = document.getElementById("passfield").value;
      var numeric = /^[0-9]+$/;

      if(number === "" || pass === ""){
        document.getElementById("errormsg").innerHTML = "Please fill your SL Number and Password!";
        document.getElementById("errormsg").style.visibility = "visible";
      }
      else if(!number.match(numeric)){
        document.getElementById("errormsg").innerHTML = "Please input numeric characters only on SL number section!";
        document.getElementById("errormsg").style.visibility = "visible";
      }
      else if(number.length < 12){
        document.getElementById("errormsg").innerHTML = "SL Number must consist 12 digits.";
        document.getElementById("errormsg").style.visibility = "visible";
      }
      else if(number.indexOf("0878")){
        document.getElementById("errormsg").innerHTML = "Please input your correct SL number. SL Number begin with 0878xxx.";
        document.getElementById("errormsg").style.visibility = "visible";
      }
      else if(pass.length < 5 || pass.length > 25){
        document.getElementById("errormsg").innerHTML = "SL Password must consist 5 to 25 characters.";
        document.getElementById("errormsg").style.visibility = "visible";
      }
      else{
        document.getElementById("errormsg").innerHTML = "no error";
        document.getElementById("errormsg").style.visibility = "hidden";
        localStorage.setItem('num', number);
        localStorage.setItem('bal', "125000");

        this.setState({toHome: true});
      }
  }

  login(){
    var temp = localStorage.getItem('num');
    if(temp){
      this.setState({toHome: true});
    }
  }

  componentDidMount() {
    this.login();
    localStorage.setItem('nav', 1);
  }
  
  render(){
    if(this.state.toHome === true){
      return <Redirect to='/home'></Redirect>
    }


    return(
      <div>
        <div className="SL-header">
          <img src={logo} className="imgLogo"></img>
          <p>Welcome to mySL</p>
        </div>
            
        <img src={bg} className="imgBg"></img>
  
        <div className="box" id="loginForm">
          <div className="boxContent">
            <p id="errormsg">no error</p>
            <div className="loginItem">
              <input type="text" placeholder="SL Number" id="numfield"></input>
            </div>
            <div className="loginItem">
              <input type="password" placeholder="Password" id="passfield"></input>
            </div>
            <button className="btn" id="btnLogin" type="submit" onClick={this.validate}>
              Login
            </button>
          </div>
          <div className="footerBox">
            <p>Forgot password</p>
          </div>
        </div>
      </div>
    );
  }
}

export default Login;

